.page-header { // высота при 767px должна быть 637px
  position: relative;
  width: 100%; 
  background: url(../img/bg-header.png) center no-repeat;
  background-size: cover;
  padding: 0 16px;

  @media (max-width: 767px) {
    background: url(../img/bg-header-mobile.png) center 100% no-repeat;
  }
}

.page-header__inner {
  width: 100%;
  max-width: 1142px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-header__inner-top {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-top: 32px;

  @media (max-width: 767px) {
    display: block;
    padding-top: 20px;
  }
}

.page-header__inner-top .page-header__phone {
  font-weight: 500;
  font-size: 15px;
  line-height: 70%;
  color: #484848;

  @media (max-width: 767px) {
    float: right;
    font-size: 14px;
    margin-top: 4px;
    margin-right: -8px;
  }
}

.page-header__logo {
  margin-right: 25px;

  @media (max-width: 767px) {
    float: left;
    width: 120px;
    height: 42px;
  }
}

.page-header__logo img {
  width: 100%;
  height: 100%;
}

.page-header__btn {
  border-bottom: 1px dashed #484848;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 500;
  font-size: 12.1px;
  line-height: 18px;
  letter-spacing: 1px;

  @media (max-width: 767px) {
    clear: right;
    float: right;
    font-size: 11.1px;
    margin-top: 13px;
    margin-right: -8px;
  }
}


.page-header__inner p {
  margin-top: 456px;
  margin-bottom: 308px;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
  width: 343px;
  text-align: center;

  @media (max-width: 767px) {
    font-size: 20px;
    line-height: 24px;
    width: 100%;
    margin-top: 270px;
    margin-bottom: 254px;
  }
}